@media (max-width: 1440px) {
  .contactMe {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;


    .title_contact {
      color: #000000;
      font-size: 40px;
      padding: 20px;
      text-align: center;
      font-weight: bold;
      margin-bottom: 80px;
    }

    .social_media {

      margin-top: 1px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      // background: linear-gradient(white, rgb(165, 165, 165), white);


      .write_social {
        color: rgb(0, 0, 0);
        font-size: 20px;
        text-align: center;
        font-weight: bold;
      }

      .media_img {
        // position: absolute;
        // margin-top: 5px;
        margin: 40px;
        width: 100px;
        height: 100px;
        // filter: sez  mjnkm pia(100%);
        // filter:sepia(100%) saturate(600%) hue-rotate(10000deg);
        filter: sepia(100%) hue-rotate(190deg) saturate(500%);

        //
        // filter: sepia(100%) saturate(500%) hue-rotate(10deg);
        &:hover {
          scroll-behavior: smooth;
          transition: 2s;
          transform: rotatey(360deg);
        }


      }

      // }


    }


  }
}

@media (max-width: 450px) {
  .contactMe {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

    .title_contact {
      color: #000000;
      font-size: 25px;
      padding: 20px;
      text-align: center;
      font-weight: bold;
      margin-bottom: 80px;
    }

    .social_media {
      width: 100%;
      margin-top: 1px;
      display: block;
      // flex-wrap: wrap;
      // justify-content: center;
      // background: linear-gradient(white, rgb(165, 165, 165), white);


      .write_social {
        color: rgb(0, 0, 0);
        font-size: 20px;
        text-align: center;
        font-weight: bold;
      }

      .media_img {
        // position: absolute;
        // margin-top: 5px;
        margin: 40px;
        width: 100px;
        height: 100px;
        // filter: sez  mjnkm pia(100%);
        // filter:sepia(100%) saturate(600%) hue-rotate(10000deg);
        filter: sepia(100%) hue-rotate(190deg) saturate(500%);

        //
        // filter: sepia(100%) saturate(500%) hue-rotate(10deg);
        &:hover {
          width: 120px;
          height: 120px;
        }


      }

      // }


    }


  }
}